import '~components/error-pages/css/error-pages.scss';
import initI18n from '~components/i18n/i18next';
import i18next from 'i18next';
import errorTemplate from '~components/error-pages/templates/error.hbs';

function generateErrorPage() {
  initI18n().then(() => {
    const html = errorTemplate({
      animationPath: '/',
      trans: {
        heading: i18next.t('404.heading.text'),
        summary: i18next.t('404.summary.text'),
        buttonHome: i18next.t('404.button.primary'),
        buttonBack: i18next.t('404.button.back'),
        search: i18next.t('404.search.heading'),
        placeholder: i18next.t('404.search.placeholder'),
      },
      homepageUrl: 'url',
    });

    document.getElementById('container').innerHTML = html;
  });
}

generateErrorPage();



